import React, { Fragment } from 'react';
import { Route, Link } from 'react-router-dom';
import WorkHome from './WorkHome';
import WorkPage from './WorkPage';

const Work = (props) => {
	console.log('m',props.match);
	return (
		<Fragment>
			<Route
				exact
				path="/work"
				component={WorkHome}
			/>
			<Route
				path={`${props.match.url}/:id`}
				component={WorkPage}
			/>
		</Fragment>
	);
}

export default Work;